@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: v.$secundario;
    color: v.$blanco;

    .footer-grid {
        padding: 5rem;
        @include m.tablet {
            @include m.grid(4, 4rem);
        }
      
    }

    .logo {
        max-width: 20rem;
        margin: 0 auto 5rem auto;
        @include m.tablet {
            margin: 0;
          
            
        }
        
    }
    .footer-widget {
        margin-bottom: 2rem;
        @include m.tablet {
            margin-bottom: 0;
        }
        h3 {
            color: v.$primario;
            text-align: center;
            @include m.tablet {
                margin-bottom: 3rem;
                text-align: left;
            } 
            
        }
    }

    nav {
            display: flex;
            flex-direction: column;
            align-items: center;
            @include m.tablet { 
                align-items: flex-start;
             }
          
            
         
       

        a {
          display: block; 
          color: v.$blanco;
          margin-bottom: 1rem;

          &:last-of-type {
              margin-bottom: 0;
          }
        }
    }
    .copyright {
        text-align: center;
        color: v.$blanco;
        margin: auto;
        border-top: 2px solid v.$primario;
        padding: 1rem;
        max-width: 120rem;
        font-size: 1.2rem;
        text-transform: uppercase;
    }
}
